.contents {
    width: 100vw;
    height: 100vh;
}

.hooks-parent {
    width: 100vw;
    position: relative;
    border: 1px solid;
    background-color: black;
}

.hooks-child {
    height: auto;
}

.component-parent {
    height: 100vh;
    width: 100vw;
    position: relative;
    border: 0px solid;
}
